// Login.js
import React, { useState } from 'react';

const Login = ({ onLogin }) => {
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');

  const handleLogin = (event) => {
    event.preventDefault();
    // En enkel autentisering, hr skulle du kunna lgga in en mer sker lsning
    if (username === 'test' && password === 'test123') {
      onLogin(true); // Stt autentisering till sann
    } else {
      alert('Felaktigt anvndarnamn eller lsenord');
    }
  };

  return (
    <div className="login">
      <h2>Logga in</h2>
      <form onSubmit={handleLogin}>
        <div>
          <label>Anvndarnamn:</label>
          <input
            type="text"
            value={username}
            onChange={(e) => setUsername(e.target.value)}
          />
        </div>
        <div>
          <label>Lsenord:</label>
          <input
            type="password"
            value={password}
            onChange={(e) => setPassword(e.target.value)}
          />
        </div>
        <button type="submit">Logga in</button>
      </form>
    </div>
  );
};

export default Login;
