import React, { useState } from 'react';

const SustainabilityPreferences = ({ onPreferencesChange, savedSettings }) => {
  const [preferences, setPreferences] = useState({
    environment: false,
    environmentPolution: false,
    social: false,
    governance: false,
  });

  const handleCheckboxChange = (event) => {
    const { name, checked } = event.target;
    const updatedPreferences = { ...preferences, [name]: checked };
    setPreferences(updatedPreferences);
    onPreferencesChange(updatedPreferences);
  };

  // Generera tooltip-innehll baserat p de valda instllningarna
  const getTooltipContent = (category) => {
    if (!savedSettings[category] || savedSettings[category].length === 0) {
      return 'Inga datapunkter valda';
    }

    return (
      <ul>
        {savedSettings[category].map((item, index) => (
          <li key={index}>{item}</li>
        ))}
      </ul>
    );
  };

  return (
    <div className="sustainability-preferences">
      <h3>Hllbarhetspreferenser</h3>

      <label>
        <input
          type="checkbox"
          name="environment"
          checked={preferences.environment}
          onChange={handleCheckboxChange}
        />
        Klimatpverkan
        <span className="tooltip">
          
          <span className="tooltiptext">Fljande EET-flt krver "Y" fr att fond ska visas om denna parameter aktiveras{getTooltipContent('environment')}</span>
        </span>
      </label>

      <label>
        <input
          type="checkbox"
          name="environmentPolution"
          checked={preferences.environmentPolution}
          onChange={handleCheckboxChange}
        />
        Miljpverkan
        <span className="tooltip">
          
          <span className="tooltiptext">Fljande EET-flt krver "Y" fr att fond ska visas om denna parameter aktiveras{getTooltipContent('environmentPolution')}</span>
        </span>
      </label>

      <label>
        <input
          type="checkbox"
          name="social"
          checked={preferences.social}
          onChange={handleCheckboxChange}
        />
        Socialt ansvar
        <span className="tooltip">
          
          <span className="tooltiptext">Fljande EET-flt krver "Y" fr att fond ska visas om denna parameter aktiveras{getTooltipContent('social')}</span>
        </span>
      </label>

      <label>
        <input
          type="checkbox"
          name="governance"
          checked={preferences.governance}
          onChange={handleCheckboxChange}
        />
        Bolagsstyrning
        <span className="tooltip">
          
          <span className="tooltiptext">Fljande EET-flt krver "Y" fr att fond ska visas om denna parameter aktiveras{getTooltipContent('governance')}</span>
        </span>
      </label>
    </div>
  );
};

export default SustainabilityPreferences;
